function initEmail() {
    const fullNameEl = document.getElementById('fullName');
    const phoneEl = document.getElementById('phone');
    const mailEl = document.getElementById('email');
    const messageEl = document.getElementById('msg');
    const submitBtn = document.getElementById('submitBtn');
    let fullName = '';
    let email = '';
    let phone = '';
    let message = '';
    let website = 'DLC management';
    function setEmailContent() {
        if (fullNameEl.value) {
            fullName = fullNameEl.value;
        }
        if (phoneEl.value) {
            phone = phoneEl.value;
        }
        if (mailEl.value) {
            email = mailEl.value;
        }
        if (messageEl.value) {
            message = messageEl.value;
        }
        return fullName + phone + email + message;
    }
    submitBtn.addEventListener('click', () => {
        setEmailContent();
        if (fullName != '' && email != '' && message != '') {
            window.location.href = `https://dlcafsendgrid20220328153355.azurewebsites.net/api/SendEmail?sender=${fullName}&email=${email}&phone=${phone}&body=${message}&website=${website}`;
        }
        else
            console.log('%cPlease fill in the required fields!', 'color:red;');
    });
}
exports.initEmail = initEmail;
